import {useState} from "react";
import {Modal} from "../components/modal/Modal";
import { registerUser } from "../services/AuthService";
import {useNavigate} from "react-router-dom";
import "../styles/login.css";
import {useOtherPagesNavBar} from "../utils/useOtherPagesNavBar";

export function Register() {
    const [email, setEmail] = useState('')
    const [password, setPassword] = useState('')
    const [initials, setInitials] = useState('')
    const [organization, setOrganization] = useState('')
    const [phone, setPhone] = useState('')
    const [disabled] = useState(false)
    const navigate = useNavigate()
    const [modalActive, setModalActive] = useState(false)
    const [error, setError] = useState('')

    useOtherPagesNavBar()
    
    const handleSubmit = async e => {
        e.preventDefault();
        try {
            const res = registerUser(initials, organization, phone, email, disabled, password);
            if (res) {
                navigate(`/login`)
            }
        } catch (e) {
            setModalActive(true);
            setError(error?.code);
        }
    }

    return (
        <>
            <div className="wrapper__login">
                <div className="container__login">
                    <div className="title__login"></div>
                    <div className="content__login">
                        <form onSubmit={handleSubmit}>
                            <div className="user-details__login">
                                <div className="input-box__login">
                                    <span className="details__login"></span>
                                    <input
                                        type="text"
                                        placeholder="  "
                                        onChange={e => setInitials(e.target.value)}
                                        value={initials}
                                        required />
                                </div>
                                <div className="input-box__login">
                                    <span className="details__login"></span>
                                    <input
                                        type="text"
                                        placeholder=" "
                                        onChange={e => setOrganization(e.target.value)}
                                        value={organization}
                                        required />
                                </div>
                                <div className="input-box__login">
                                    <span className="details__login"></span>
                                    <input
                                        type="text"
                                        placeholder="  "
                                        onChange={e => setPhone(e.target.value)}
                                        value={phone}
                                        required />
                                </div>
                                <div className="input-box__login">
                                    <span className="details__login"></span>
                                    <input
                                        type="email"
                                        placeholder="   "
                                        onChange={e => setEmail(e.target.value)}
                                        value={email}
                                        required />
                                </div>
                                <div className="input-box__login">
                                    <span className="details__login"></span>
                                    <input
                                        type="password"
                                        placeholder=" "
                                        onChange={e => setPassword(e.target.value)}
                                        value={password}
                                        required />
                                </div>
                                <div className="input-box__login">
                                    <span className="details__login"> ...</span>
                                    <div style={{display: 'flex'}}>
                                        <input
                                            style={{width: "45px", height: "45px"}}
                                            type="checkbox"
                                            placeholder=" "
                                            required /> 
                                        <p className="checkbox">
                                        <a href="/agreement" target="_blank">
                                            
                                        </a>
                                        </p>
                                    </div>
                                </div>
                            </div>
                            <div className="button__login">
                                <input type="submit" value="" />
                            </div>
                        </form>
                    </div>
                </div>
            </div>
            <Modal active={modalActive} setActive={setModalActive}>
                <p>
                    {error}
                </p>
            </Modal>

        </>
    )
}
