import {useState} from "react";
import {loginUser, setToken} from "../services/AuthService";
import {Modal} from "../components/modal/Modal";
import {useNavigate} from "react-router-dom";
import "../styles/login.css";
import {useOtherPagesNavBar} from "../utils/useOtherPagesNavBar";
export function Login({setIsAuth}) {
    const [email, setEmail] = useState('')
    const [password, setPassword] = useState('')
    const [modalActive, setModalActive] = useState(false)
    const [error, setError] = useState('')
    const navigate = useNavigate()

    useOtherPagesNavBar()

    const handleSubmit = async (e) => {
        e.preventDefault();
        const token = loginUser(email, password);
        token
            .then((data) =>
                {
                    setToken(data?.data?.access_token)
                    setIsAuth(true)
                    navigate(`/`)
                })
            .catch((error) => {
                console.log(error)
                setModalActive(true);
                setError(error?.code);
            });
    }

    return (
        <>
            <div className="wrapper__login">
                <div className="container__login">
                    <div className="title__login"></div>
                    <div className="content__login">
                        <form onSubmit={handleSubmit}>
                            <div className="user-details__login">
                                <div className="input-box__login">
                                    <span className="details__login"></span>
                                    <input
                                        type="email"
                                        placeholder="   "
                                        onChange={e => setEmail(e.target.value)}
                                        value={email}
                                        required />
                                </div>
                                <div className="input-box__login">
                                    <span className="details__login"></span>
                                    <input
                                        type="password"
                                        placeholder=" "
                                        onChange={e => setPassword(e.target.value)}
                                        value={password}
                                        required />
                                </div>
                            </div>
                                <div className="button__login">
                                    <input type="submit" value="" />
                                </div>
                            </form>
                        <div className="input-box__login">
                            <input value="" type="submit" onClick={() => {
                                navigate(`/register`)
                            }} />
                        </div>
                        </div>
                    </div>
                </div>
                <Modal active={modalActive} setActive={setModalActive}>
                    <p>
                        {error}
                    </p>
                </Modal>

        </>
    )
}
