import "./style.css"
import tg from "../../img/icons/tg.png";

import twitter from "../../img/icons/twitter.svg";
import gitHub from "../../img/icons/gitHub.svg";
import linkedIn from "../../img/icons/linkedIn.svg";
export function Footer() {
    return (
        <footer className="footer">
            <div className="container">
                <div className="footer__wrapper">
                    <ul className="social">
                        <li className="social__item"><a rel="noreferrer"  target="_blank" href="https://t.me/NIIZHB_bot"><img style={{width: "38px"}} src={tg} alt="Link"/></a></li>
                        <li className="social__item"><a href="#!"><img style={{width: "38px"}} src={tg} alt="Link"/></a>
                        </li>
                        <li className="social__item"><a href="#!"><img src={twitter} alt="Link"/></a>
                        </li>
                        <li className="social__item"><a href="#!"><img src={gitHub} alt="Link"/></a></li>
                        <li className="social__item"><a href="#!"><img src={linkedIn} alt="Link"/></a>
                        </li>
                    </ul>
                    <div className="copyright">
                        <p> 2023 github.com/molch4nov</p>
                    </div>
                </div>
            </div>
        </footer>
    )
}
