import { AnalyticsCard } from "../components/analyticsCard/AnalyticsCard";
import { DragDropFiles } from "../components/dropzone/DragDropFiles";
import Download from "../img/cards/download.png";
import Type from "../img/cards/type.png";
import Load from "../img/cards/load.png";
import {useOtherPagesNavBar} from "../utils/useOtherPagesNavBar";
import "../styles/analytics.css"
import {useState} from "react";

export function Analytics({isAuth, setIsAuth}) {
  useOtherPagesNavBar()
  const [starting, setStarting] = useState(true)


  return (

    <div className="bg-white py-16 sm:py-8">
    <div className="mx-auto max-w-7xl px-6 lg:px-8">
      <h2 className="title_landing">
           
      </h2>
      {starting && <div className="mx-auto mt-10 grid max-w-lg grid-cols-4 items-center gap-x-8 gap-y-10 sm:max-w-xl sm:grid-cols-3 sm:gap-x-10 lg:mx-0 lg:max-w-none lg:grid-cols-3">
        <AnalyticsCard type={"download"} src={Download} title={""} text={" ,    "}/>
        <AnalyticsCard src={Type} title={""} text={"     "}/>
        <AnalyticsCard type={"load"} src={Load} title={""} text={"    "}/>
      </div>}

      {starting && <div className="title_form_ready">
           ?  
      </div>}

      <DragDropFiles setIsAuth={setIsAuth} setStarting={setStarting} />
    </div>
  </div>
  )
};
