import { useState, useRef } from "react";
import {getToken, logoutUser} from "../../services/AuthService";
import {Modal} from "../modal/Modal";
import "../../styles/radio.css"
import "./style.css"
import "../../styles/files.css"
import "../../styles/graphs.css"
import axios from "axios";
import Plotly from 'plotly.js-dist';


export const DragDropFiles = ({setIsAuth, setStarting}) => {
  const transpose = matrix => matrix[0].map((col, i) => matrix.map(row => row[i]));
  const [file, setFiles] = useState(null)
  let resData = Object, graphData = Object;
  let arr;
  const [grades, setGrades] = useState([])
  const [sizes, setSizes] = useState([])
  const [check, setCheck] = useState(false)
  const [lastSize, setLastSize] = useState(0)
  const [lastGrade, setLastGrade] = useState(0)
  const [numGrade, setNumGrade] = useState(-1)
  const [numSize, setNumSize] = useState(-1)
  const [modalActive, setModalActive] = useState(false)
  const [error, setError] = useState('')
  const inputRef = useRef();
  const [waiting, setWaiting] = useState(false)


  const handleDragOver = (event) => {
    event.preventDefault();
  };

  const handleDrop = (event) => {
    event.preventDefault();
    setFiles(event.dataTransfer.files)
    setStarting(false)
  };

  const handleUpload = async () => {
    try {
      setWaiting(true)
      const token = getToken();
      const res = await axios.post('http://localhost:8000/api/v1/analysis/selectors/',
        {file: file?.[0] }
        , {
          headers: {
            Authorization: `Bearer ${token}`,
            'Accept': 'application/json',
            'Content-Type': 'multipart/form-data'
          },
        })
      resData = res?.data;
      setGrades(Object.values(resData)[0])
      setSizes(Object.values(resData)[1])
      setWaiting(false);
    } catch (e) {
      if(e?.response.status === 401) {
        setModalActive(true);
        setWaiting(false);
        setError(" .  .");
        setIsAuth(false);
        setTimeout(logoutUser, 3000)
      }
    }

  };

  const handleSelectors = async () => {
    setWaiting(true)
    try {
      const token = getToken();
      const res = await axios.post(`http://localhost:8000/api/v1/analysis/stats`,
        {file: file?.[0] }
        , {
          headers: {
            Authorization: `Bearer ${token}`,
            'Accept': 'application/json',
            'Content-Type': 'multipart/form-data'
          },
          params: {
            size: `${numSize.toString()}`,
            grade: `${numGrade.toString()}`
          }
        })
      graphData = res?.data;
      setCheck(true)
      arr = Array.from(Object.values(graphData))

      let hists = (Object.values(Object.values(arr[0]))[0])
      let table1 = Object.values(Object.values(Object.values(arr[1]))[0])
      let table23 = (Object.values(Object.values(arr[2])))

      console.log(table23)

      const colors = ['#636EFA', '#EF553B', '#00CC96', '#AB63FA', '#FFA15A', '#19D3F3', '#FF6692', '#B6E880', '#FF97FF', '#FECB52']
      const names = [ ' ',
                      ' ',
                      '  ', 
                      '    ', 
                      ' ']
      //table2
      let y = [names]
      let help = []
      //table3
      let z = [names]
      let help_z = []
      //table4
      let d = [[' ',
                ' ',
                '  ',
                ' ']]
      let help_d = []

      for (let i = 0; i < 5; i++) {
        let alpha = Object.values(Array.from(  Object.values( Object.values(Object.values(Array.from(  Object.values( Object.values(table23)    )     )[2]))    )     )[i])
        let betta = Object.values(Array.from(  Object.values( Object.values(Object.values(Array.from(  Object.values( Object.values(table23)    )     )[3]))    )     )[i])

        //its block for histograms
        let x = Array.from(  Object.values( Object.values(hists)    )     )[i]
        let layout = {
          title: (Object.keys(hists))[i],
          autosize: false,
          height: 300,
          width: 600,
          margin: {
            l: 50,
            r: 0,

          },
          automargin: false,
        };
        x = (Object.values(x)[0])
        let trace = {
          x: x,
          type: 'histogram',
          histnorm: 'probability',
          name: (Object.keys(hists))[i],
          marker: {
            color: colors[i]
          }
        };

        let trace_norm = {
          x: alpha[0],
          y: alpha[1],
          type: 'scatter',
          name: " ",
          marker: {
            color: "black"
          }
        }

        let trace_opt = {
          x: betta[0],
          y: betta[1],
          type: 'scatter',
          name: " ",
          marker: {
            color: colors[colors.length - i]
          }
        }

        let data = [trace, trace_norm, trace_opt];
        Plotly.newPlot(`hist_${i}`, data, layout);


        //its block for table
        help.push( Object.values(Array.from(  Object.values( Object.values(table1)    )     )[i]))
        help_z.push(Object.values(Array.from(  Object.values( Object.values(Object.values(Array.from(  Object.values( Object.values(table23)    )     )[0]))    )     )[i]))
        if(i !== 4) {
          help_d.push(Object.values(Array.from(  Object.values( Object.values(Object.values(Array.from(  Object.values( Object.values(table23)    )     )[1]))    )     )[i]))
        }
      }

      y.push(...transpose(help))
      z.push(...transpose(help_z))
      d.push(...transpose(help_d))

      let table_data = [{
        type: 'table',
        header: {
          values: [["<b></b>"], ["<b> </b>"],
            ["<b> -</b>"], ["<b> -</b>"]],
          align: ["left", "center"],
          line: {width: 1, color: '#506784'},
          fill: {color: '#119DFF'},
          font: {family: "Arial", size: 12, color: "white"}
        },
        cells: {
          values: y,
          align: ["left", "center"],
          line: {color: "#506784", width: 1},
          fill: {color: ['#25FEFD', 'white']},
          font: {family: "Arial", size: 11, color: ["#506784"]}
        }
      }]

      let table_data_z = [{
        type: 'table',
        header: {
          values: [["<b></b>"], ["<b>  </b>"],
            ["<b>  - (/)</b>"]],
          align: ["left", "center"],
          line: {width: 1, color: '#506784'},
          fill: {color: '#119DFF'},
          font: {family: "Arial", size: 12, color: "white"}
        },
        cells: {
          values: z,
          align: ["left", "center"],
          line: {color: "#506784", width: 1},
          fill: {color: ['#25FEFD', 'white']},
          font: {family: "Arial", size: 11, color: ["#506784"]}
        }
      }]

      let table_data_d = [{
        type: 'table',
        header: {
          values: [["<b></b>"], ["<b></b>"],
            ["<b></b>"]],
          align: ["left", "center"],
          line: {width: 1, color: '#506784'},
          fill: {color: '#119DFF'},
          font: {family: "Arial", size: 12, color: "white"}
        },
        cells: {
          values: d,
          align: ["left", "center"],
          line: {color: "#506784", width: 1},
          fill: {color: ['#25FEFD', 'white']},
          font: {family: "Arial", size: 11, color: ["#506784"]}
        }
      }]


      Plotly.newPlot('table1', table_data);
      Plotly.newPlot('table2', table_data_z);
      Plotly.newPlot('table3', table_data_d);


    } catch (e) {
      if(e?.response.status === 401) {
        setModalActive(true);
        setError(" .  .");
        setIsAuth(false);
        setTimeout(logoutUser, 3000)
      }
    }
  }

  if((grades?.length > 0) && (sizes?.length > 0) ) return (
    <>
      {!check && <div className="form_radio_wrapper">
      <ul className="form_radio_group">
        {grades.map(
          (grade, index) => <li className="form_radio_group-item"><input id={`radio1-${index}`} type="radio" name="radio" value={index}/>
                          <label onClick={() => {
                            document?.getElementById(`radio1-${lastGrade}`)?.removeAttribute('checked')
                            setNumGrade(grade)
                            setLastGrade(index)
                            document?.getElementById(`radio1-${index}`)?.setAttribute('checked', true)
                          }} form={`radio-${index}`}>{grade}</label>
                      </li>
        )}
      </ul>
      <ul className="form_radio_group">
        {(numGrade !== -1) && sizes.map(
          (size, index) => <li className="form_radio_group-item"><input id={`radio2-${(index+1) * 20}`} type="radio" name="radio2" value={(index+1)*20} />
                          <label onClick={() => {
                            document?.getElementById(`radio2-${lastSize}`)?.removeAttribute('checked')
                            setNumSize(size)
                            setLastSize((index+1) * 20)
                            document?.getElementById(`radio2-${(index + 1) * 20}`)?.setAttribute('checked', true)
                          }} form={`radio-${(index+1)*20}`}>{size}</label>
                      </li>
        )}
      </ul>
      {(numSize !== -1) && !waiting && <button className="upload" onClick={handleSelectors}></button>}
      {(numSize !== -1) && waiting && <button className="upload">...</button>}

    </div>}

      <div className="plot_wrapper grid max-w-lg grid-cols-2 items-center">
        <div id='hist_0' className="plot_hist"></div>
        <div id='hist_1' className="plot_hist"></div>
        <div id='hist_2' className="plot_hist"></div>
        <div id='hist_3' className="plot_hist"></div>
        <div id='hist_4' className="plot_hist"></div>

      </div>

      <div className="plot_wrapper grid max-w-lg grid-cols-1 grid-rows-3 items-center table_wrapper">
        <div id="table1" className="plot_table"></div>
        <div id="table2" className="plot_table"></div>
        <div id="table3" className="plot_table"></div>
      </div>

      <Modal active={modalActive} setActive={setModalActive}>
        <p>
          {error}
        </p>
      </Modal>


    </>


  )

  if (file && (grades?.length === 0)) return (
    <div className="uploads">
        <ul className="files_list">
            {Array.from(file).map((file, idx) => <li className="files_list_item" key={idx}>{file.name}</li> )}
        </ul>
        <div className="actions">
            <button className="cancel" onClick={() => {setFiles(null); setStarting(true);}}></button>
            {!waiting && <button className="upload" onClick={handleUpload}></button> }
            {waiting && <button className="upload" >...</button> }
        </div>
      <Modal active={modalActive} setActive={setModalActive}>
        <p>
          {error}
        </p>
      </Modal>
    </div>
  )

  return (
    <>
        <div 
            className="dropzone"
            onDragOver={handleDragOver}
            onDrop={handleDrop}
        >
          <h1>  </h1>
          <h1></h1>
          <input 
            type="file"
            multiple
            onChange={(event) => setFiles(event.target.files)}
            hidden
            ref={inputRef}
          />
          <button className="bg-gray-300" onClick={() => inputRef.current.click()}> </button>
        </div>
        <Modal active={modalActive} setActive={setModalActive}>
                <p>
                    {error}
                </p>
        </Modal>
    </>
  );
};
